@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #ffffff;
  color: #4a4a4a;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2 {
  color: #4a4a4a;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  background-color: #ffffff;
}

.form-label {
  font-weight: bold;
}

.status-open {
  background-color: #28a745;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 27px;
}

.status-closed {
  background-color: #dc3545;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 27px;
}

.list-group-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-button {
  margin-left: 10px;
}

button {
  margin: 5px 0;
}

.table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  margin-top: 20px;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  overflow: hidden;
}

.table th, .table td {
  border: none;
  padding: 15px;
  text-align: center;
  vertical-align: middle;
}

.table th {
  background-color: #f8f9fa;
  font-weight: 700;
}

.table td {
  background-color: #ffffff;
  border-top: 1px solid #d3d3d3;
}

.table tr:nth-child(even) td {
  background-color: #f2f2f2;
}

.table tr:last-child td {
  border-bottom: none;
}

.table .status-open {
  background-color: #28a745;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 27px;
}

.table .status-closed {
  background-color: #dc3545;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 27px;
}

.navbar {
  background-color: #4a4a4a;
  padding: 1rem;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin: 0;
}

.navbar ul li {
  margin: 0 1rem;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.navbar ul li a:hover {
  text-decoration: underline;
}

.btn {
  border-radius: 15px;
  font-family: 'Montserrat', sans-serif;
}

.btn-primary {
  background-color: #ff5e01;
  border-color: #ff5e01;
}

.btn-primary:hover {
  background-color: #e04e00;
  border-color: #e04e00;
}

.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
}

.btn-secondary:hover {
  background-color: #5a6268;
  border-color: #545b62;
}

.btn-outline-secondary {
  border-color: #6c757d;
  color: #6c757d;
}

.btn-outline-secondary:hover {
  background-color: #f8f9fa;
}

.btn-success {
  background-color: #28a745;
  border-color: #28a745;
}

.btn-success:hover {
  background-color: #218838;
  border-color: #1e7e34;
}

.btn-danger {
  background-color: #dc3545;
  border-color: #dc3545;
}

.btn-danger:hover {
  background-color: #c82333;
  border-color: #bd2130;
}

.btn-warning {
  background-color: #ffc107;
  border-color: #ffc107;
}

.btn-warning:hover {
  background-color: #e0a800;
  border-color: #d39e00;
}

.action-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
  list-style: none;
  margin-top: 20px;
}

.page-item {
  margin: 0 5px;
}

.page-link {
  color: #007bff;
  text-decoration: none;
  padding: 8px 12px;
  border: 1px solid #dee2e6;
  border-radius: 4px;
}

.page-link:hover {
  background-color: #e9ecef;
  color: #0056b3;
  border-color: #dee2e6;
}
